import { emoji, action } from "../_util"
export default (creep, rawTarget, type = RESOURCE_ENERGY) => {
	if (!rawTarget) return false;
	let target = rawTarget;
	console.log(target instanceof Array);
	if (target instanceof Array) {
		target = _.compact(target);
		if (target.length == 0) return false;
		target = target[0];
	};
	console.log(target instanceof Array);
	action(creep, target, creep.transfer(target[0], RESOURCE_ENERGY), emoji.transfer);
	return true;
}


